
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

body,html{
  margin: 0;
  padding: 0;
}
#app{
  margin:0
}
button{
  margin: 10px;
}
.list{
  margin:10px
}
.li{
  padding-bottom: 4px;
  text-align: left;
  background: powderblue;
  margin-top: 5px;
  border-radius: 5px;
}
.title{
  padding-right: 30px;
  -webkit-box-flex:1;
      -ms-flex:1;
          flex:1
}
.tiaojian{
  background: lightgrey;
}
.button-box{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: right;
      -ms-flex-pack: right;
          justify-content: right;
}
.button-box button{
  -webkit-box-flex:1;
      -ms-flex:1;
          flex:1
}
